export default function ServicesPage() {
  const services = [
    { title: 'Web Development', desc: 'Next.js, React, Node.js, Tailwind' },
    { title: 'UI/UX Design', desc: 'Design systems, wireframes, prototyping' },
    { title: 'Branding', desc: 'Identity, tone, and digital experience strategy' },
  ]

  return (
    <main className="py-20">
      <div className="container mx-auto">
        <h1 className="text-4xl font-bold mb-6">Services</h1>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {services.map(s => (
            <div
              key={s.title}
              className="p-6 bg-white/60 backdrop-blur rounded-lg shadow hover:shadow-lg transition"
            >
              <h3 className="text-xl font-semibold">{s.title}</h3>
              <p className="text-gray-600 mt-2">{s.desc}</p>
            </div>
          ))}
        </div>
      </div>
    </main>
  )
}
